
.header{display:flex;padding:25px 60px;height:110px
}
.header__logo{margin-right:65px;width:175px;height:42px;background:url(logo.png) no-repeat
}
.header__list{flex:1;padding:15px;height:100%
}
.header__list li{padding-right:2.6%;float:left;height:100%
}
.header__list li.current{position:relative
}
.header__list li.current:after{position:absolute;bottom:0;left:0;width:24px;height:4px;background-color:#f18871;content:""
}
.header__list li h3,.header__list li p{display:inline-block
}
.header__list li h3{font-size:15px;color:#2f2f2d;font-weight:600
}
.header__list li p{font-size:12px;color:#2f2f2d
}



