
.container{overflow:hidden
}
.banner{height:806px
}
.pic-box1 .tit{margin:0 auto;width:670px;height:177px;background:url(box1-tit.png) no-repeat;background-size:670px 177px
}
.pic-box1 ul{display:flex;margin-top:70px;width:100%
}
.pic-box1 ul li{flex:1;cursor:pointer
}
.pic-box1 ul li img{display:block;width:100%;height:auto
}
.pic-box1 ul li:hover,.pic-box1 ul li.current{position:relative
}
.pic-box1 ul li:hover:after,.pic-box1 ul li.current:after{position:absolute;content:"";left:0;right:0;bottom:0;height:4px;background-color:#ee6a4d
}
.pic-box2{padding-bottom:70px;position:relative;background-color:#f9f7f6
}
.pic-box2 .btns{position:absolute;left:20px;right:20px;bottom:70px
}
.pic-box2 .btns>i{width:45px;height:45px;background-color:red
}
.pic-box2 .btns .btn-prev{float:left
}
.pic-box2 .btns .btn-next{float:right
}
.pic-box2 .tit{margin:78px auto;width:670px;height:168px;background:url(box2-tit.png) no-repeat;background-size:670px 168px
}
.pic-box2 .num{display:flex;position:absolute;width:62px;height:62px;background-color:#fff;font-size:32px;font-style:italic;align-items:center;justify-content:center
}
.pic-box2 .pic-block-big{height:570px
}
.pic-box2 .pic-block-big .pic-item{float:left;width:50%;height:100%
}
.pic-box2 .pic-block-big .pic-item li{float:left;width:50%;height:50%
}
.pic-box2 .pic-block-big .pic-item-1{position:relative
}
.pic-box2 .pic-block-big .pic-item-1 img{margin-left:8px;display:block;width:70%;height:100%
}
.pic-box2 .pic-block-big .pic-item-1 .bg{position:absolute;right:0;top:0;width:40%;width:531px;height:286px;background:url(box2/1-tit.png) no-repeat
}
.pic-box2 .pic-block-big .pic-item-2 img{display:block;max-width:100%;height:100%
}
.pic-box2 .pic-block-big .pic-item-2 li{position:relative
}
.pic-box2 .pic-block-small .pic-item{float:left;width:50%
}
.pic-box2 .pic-block-small .pic-item img{display:block;max-width:100%;height:auto
}
.pic-box2 .pic-block-small .pic-item-3 .bg{float:right;width:50%;height:100%
}
.pic-box2 .pic-block-small .pic-item-4 .bg{position:relative;float:left;width:50%;height:100%
}
.pic-box3 img{display:block;width:100%;height:auto
}
.pic-box4{padding:75px 2.5%
}
.pic-box4 .main{padding:0 55px;height:100%;background-color:#f9f7f6
}
.pic-box4 .main__hd{margin-top:70px;margin-left:auto;margin-right:auto;margin-bottom:40px;width:555px;height:110px;background:url(box4-tit.png) no-repeat;background-size:contain
}
.pic-box4 .main__bd{text-align:center
}
.pic-box4 .main__bd li{display:inline-block;width:17%;height:210px
}
.pic-box4 .main__bd li img{max-width:100%;height:100%
}
.pic-box4 .main__bd .link{position:relative;margin-top:35px;width:100%;height:4px;background-color:#e6e0dc
}
.pic-box4 .main__bd .link>i{position:absolute;left:0;top:0;bottom:0;width:17%;background-color:#ee6a4d
}
.pic-box4 .main__bd .desc{padding-top:15px;padding-bottom:35px
}
.ft-btns{color:#fff
}
.ft-btns .btn{float:left;width:50%;height:55px;line-height:55px;text-align:center
}
.ft-btns .btn.like{background-color:#ee6a4d
}
.ft-btns .btn.share{background-color:#f08068
}
.item_box{width:100%;padding:0px 0px 82px 0px;overflow:hidden
}
.item_box_wp{width:1180px;margin:0 auto
}
.item_box_wp .title h1{font-size:36px;color:#3f616c
}
.item_box_wp .title h2{font-size:18px;color:#aaa
}
.item_box_wp h3{color:#67b83d;font-size:36px;margin:74px 0px 50px 0px
}
.item_box_wp h4{color:#3f616c;font-size:36px;margin:65px 0px 25px 0px;text-align:center
}
.voice_2{width:100%;position:relative;left:50%;transform:translateX(-50%)
}
.voice_2 ul li{float:left;overflow:hidden;height:806px;color:#fff;cursor:pointer;position:relative
}
.voice_2 .unfold{display:flex;justify-content:center;align-items:center;position:absolute;left:0;right:0;bottom:0;height:215px;background:#fff;color:#3e3d3b;flex-direction:column
}
.voice_2 .unfold h3{font-size:24px
}
.voice_2 .unfold p{font-size:16px
}
.voice__hd img{display:block
}



